body {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
}

:root {
  --blue-color: #00acdc;
}

.header {
  box-sizing: border-box;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 0 50px;
  display: flex;
}

.logo {
  flex-shrink: 0;
  height: 40px;
  margin-right: auto;
  display: block;
}

.nav__list {
  text-transform: uppercase;
  flex-wrap: nowrap;
  list-style: none;
  display: flex;
}

.nav__link {
  color: #000;
  flex-shrink: 0;
  height: 15px;
  margin: 22px 20px;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  position: relative;
}

.nav__link:hover {
  color: var(--blue-color);
}

.is-active:after {
  content: "";
  background-color: var(--blue-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: -18px;
  left: 50%;
  transform: translateX(-50%);
}

@media (width <= 1024px) {
  .header {
    padding: 0 20px;
  }

  .logo {
    margin-right: 243px;
  }
}
